*,
*:before,
*:after {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.bg-color {
  background: linear-gradient(to bottom right, #007fc2, #00d6ee);
}

.container {
  width: 1200px;
  padding: 0 10px;
  margin: 0 auto;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  margin-top: -1px;
  margin-left: -1px;
  overflow: hidden;
  position: absolute;
  clip: rect(0, 0, 0, 0);
}

.header {
  font-family: sans-serif;
  font-weight: 700;
  color: #fff;
  font-size: 50px;
  padding-top: 100px;
  padding-bottom: 50px;
}

.card {
  padding: 50px 30px 60px 30px;
  width: 350px;
  background: #fff;
  border-radius: 2%;
  margin-left: 15px;
  margin-bottom: 45px;
  list-style-type: none;
}

.card_photo {
  border-radius: 50%;
}

.card_title {
  padding-bottom: 10px;
  padding-top: 20px;
  font-size: 35px;
  font-weight: 700;
}

.card_feedback {
  font-size: 17px;
  padding-bottom: 25px;
}

.card_date {
  display: block;
  margin-top: 10px;
  font-size: 20px;
  color: grey;
  font-style: italic;
}